import React, { useCallback, useMemo, useState } from "react";

import { Box, Button, ButtonGroup, Dialog, DialogActions, DialogContent, DialogTitle } from "@mui/material";
import { IMidiAnimationJSON } from '../../midi/types';
import { BaseAnimationOptions, OptionsPrototype } from "../../midi/animations/types";
import SimpleAnimationEditor from "./SimpleAnimationEditor";

interface AnimationAndOptions {
  animation: IMidiAnimationJSON;
  optionsPrototype: OptionsPrototype;
}

interface ControlledSimpleAnimationEditorProps {
  animation: AnimationAndOptions;
  onSave: (newOptions: BaseAnimationOptions) => void;
}

// TODO: Move this
export const useSimpleAnimationEditor = (props: ControlledSimpleAnimationEditorProps) => {
  const { animation, onSave } = props;

  const [changed, setChanged] = useState(false);
  const initialOptions = useMemo(() => animation?.animation?.options || {}, [animation]);
  const [optionsToEdit, setOptionsToEdit] = useState<BaseAnimationOptions>(initialOptions);

  const onOptionsChange = (options: BaseAnimationOptions) => {
    const changed = JSON.stringify(options) !== JSON.stringify(initialOptions);
    console.log("onOptionsChange", changed)
    setChanged(changed);
    setOptionsToEdit(options);
  };

  const editor = <SimpleAnimationEditor
    options={initialOptions}
    onOptionsChange={onOptionsChange}
    optionsPrototype={animation?.optionsPrototype}
  />;

  const onSubmit = useCallback(() => {
    onSave(optionsToEdit);
  }, [onSave, optionsToEdit]);

  return {
    changed,
    editor,
    initialOptions,
    onOptionsChange,
    onSubmit,
  }
}

interface EditDialogProps {
  isOpen: boolean;
  onSave: (newOptions: BaseAnimationOptions) => void;
  onClose?: () => void;
  animation: AnimationAndOptions;
}

const EditDialog = (props: EditDialogProps) => {
  const { isOpen, onSave, onClose, animation } = props;

  const { onSubmit, changed, initialOptions, onOptionsChange } = useSimpleAnimationEditor({
    animation,
    onSave,
  });

  return (
    <Dialog
      fullWidth
      maxWidth={"lg"}
      open={isOpen}
      onClose={onClose}
      PaperProps={{
        component: "form",
        onSubmit,
      }}
    >
      <DialogTitle>
        {animation?.animation?.name} ({animation?.animation?.id})
      </DialogTitle>
      <DialogContent>
        <Box
          sx={{
            height: 600,
          }}
        >
          <SimpleAnimationEditor
            options={initialOptions}
            onOptionsChange={onOptionsChange}
            optionsPrototype={animation?.optionsPrototype}
          />
        </Box>
      </DialogContent>
      <DialogActions>
        <ButtonGroup>
          <Button
            onClick={onClose}
          >
            Cancel
          </Button>
          <Button
            type="submit"
            disabled={!changed}
          >Save
          </Button>
        </ButtonGroup>
      </DialogActions>
    </Dialog>
  )
}

export default EditDialog;